
import Swiper from 'vux/src/components/swiper/swiper.vue'


const json01 = {code: 'slide1'}
const json05 = {code: 'slide3'}
const json02 = {code: 'p11'}
const json03 = {code: 'p13'}
const json04 = {code: 'p12'}

export default {
components: {
Swiper
},
data () {
return {
imgU: require('../assets/images/index/m-t.jpg'),
banners1: [],
banners2: [],
moms: [],
babies: [],
trails: []
}
},
destroyed () {
console.log('index destroyed')

},
beforeDestroy () {
console.log('index beforeDestroyed')

},
created () {

/* let result = await this.axios.get('index.json')
let banner1 = result.data.banner1 */
this.getData('banners1', json01)
this.getData('banners2', json05)
this.getData('moms', json02)
this.getData('babies', json03)
this.getData('trails', json04)
this.getData('trails', json04)

},
methods: {
async getData (target, json) {
let result = await this.axios.post('/web/adlist', json)
let data = result.data
let lists = []
data.map((item, index) => {
if (item.status) {
lists.push({
url: item.linkurl,
img: this.base_img + item.img,
title1: item.title
})
}
})

switch (target) {
case 'banners1':
this.banners1 = lists
break
case 'banners2':
this.banners2 = lists
break
case 'moms':
this.moms = lists
break
case 'babies':
this.babies = lists
break
case 'trails':
this.trails = lists
break
}
}
}
}



